"use client";

import Link from 'next/link';
import { motion } from 'framer-motion';
import { CSSProperties, useState } from 'react';

export default function CTA() {
  const [formState, setFormState] = useState({
    name: '',
    email: '',
    phone: '',
    message: ''
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormState(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Form submission logic would go here
    console.log('Form submitted:', formState);
    alert('Thank you for your message. We will be in touch shortly.');
  };

  const sectionStyle: CSSProperties = {
    backgroundColor: 'var(--background)',
    paddingTop: '8rem',
    paddingBottom: '8rem',
    position: 'relative',
    zIndex: 8,
    borderBottom: '1px solid rgba(255, 255, 255, 0.05)'
  };

  const containerStyle: CSSProperties = {
    maxWidth: '80rem',
    marginLeft: 'auto',
    marginRight: 'auto',
    paddingLeft: '1.5rem',
    paddingRight: '1.5rem'
  };

  const headingStyle: CSSProperties = {
    fontSize: '2.5rem',
    fontWeight: '200',
    marginBottom: '1rem',
    textAlign: 'center',
    letterSpacing: '0.1em'
  };

  const accentSpanStyle: CSSProperties = {
    position: 'relative',
    color: 'var(--primary)',
    fontWeight: '300',
    display: 'inline-block',
    borderBottom: '1px solid var(--primary)',
    paddingBottom: '0.25rem'
  };

  const subheadingStyle: CSSProperties = {
    fontSize: '1.1rem',
    color: 'var(--muted-100)',
    marginBottom: '5rem',
    maxWidth: '42rem',
    marginLeft: 'auto',
    marginRight: 'auto',
    textAlign: 'center',
    lineHeight: '1.8',
    fontWeight: '300',
    letterSpacing: '0.025em'
  };

  const formContainerStyle: CSSProperties = {
    maxWidth: '42rem',
    margin: '0 auto',
    border: '1px solid rgba(255, 255, 255, 0.05)',
    padding: '3rem',
    backgroundColor: 'rgba(255, 255, 255, 0.01)'
  };

  const formGroupStyle: CSSProperties = {
    marginBottom: '1.5rem'
  };

  const labelStyle: CSSProperties = {
    display: 'block',
    marginBottom: '0.5rem',
    fontSize: '0.85rem',
    fontWeight: '300',
    letterSpacing: '0.1em',
    textTransform: 'uppercase',
    color: 'var(--foreground)'
  };

  const inputStyle: CSSProperties = {
    width: '100%',
    padding: '0.75rem 1rem',
    backgroundColor: 'transparent',
    border: '1px solid rgba(255, 255, 255, 0.1)',
    color: 'var(--foreground)',
    fontSize: '0.95rem',
    lineHeight: '1.5',
    transition: 'border-color 0.3s, box-shadow 0.3s',
    fontWeight: '300'
  };

  const textareaStyle: CSSProperties = {
    ...inputStyle,
    minHeight: '8rem',
    resize: 'vertical'
  };

  const buttonStyle: CSSProperties = {
    width: '100%',
    padding: '0.75rem 2rem',
    backgroundColor: 'transparent',
    color: 'var(--primary)',
    border: '1px solid var(--primary)',
    fontWeight: '300',
    display: 'inline-block',
    textAlign: 'center',
    transition: 'all 0.3s',
    textDecoration: 'none',
    fontSize: '0.85rem',
    letterSpacing: '0.15em',
    textTransform: 'uppercase',
    cursor: 'pointer',
    marginTop: '1rem'
  };

  const formRowStyle: CSSProperties = {
    display: 'grid',
    gridTemplateColumns: 'repeat(2, 1fr)',
    gap: '1.5rem',
    marginBottom: '1.5rem'
  };

  return (
    <section id="contact" style={sectionStyle}>
      <div style={containerStyle}>
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
        >
          <h2 style={headingStyle}>
            Get in <span style={accentSpanStyle}>Touch</span>
          </h2>
          <p style={subheadingStyle}>
            Ready to elevate your business with our AI solutions? Contact us today to discuss how we can help transform your operations.
          </p>
        </motion.div>
        
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.2 }}
          viewport={{ once: true }}
        >
          <div style={formContainerStyle}>
            <form onSubmit={handleSubmit}>
              <div style={formRowStyle}>
                <div style={formGroupStyle}>
                  <label htmlFor="name" style={labelStyle}>Name</label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    placeholder="Your name"
                    required
                    style={inputStyle}
                    value={formState.name}
                    onChange={handleChange}
                    onFocus={(e) => e.target.style.borderColor = 'var(--primary)'}
                    onBlur={(e) => e.target.style.borderColor = 'rgba(255, 255, 255, 0.1)'}
                  />
                </div>
                <div style={formGroupStyle}>
                  <label htmlFor="email" style={labelStyle}>Email</label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    placeholder="Your email"
                    required
                    style={inputStyle}
                    value={formState.email}
                    onChange={handleChange}
                    onFocus={(e) => e.target.style.borderColor = 'var(--primary)'}
                    onBlur={(e) => e.target.style.borderColor = 'rgba(255, 255, 255, 0.1)'}
                  />
                </div>
              </div>
              
              <div style={formGroupStyle}>
                <label htmlFor="phone" style={labelStyle}>Phone</label>
                <input
                  type="tel"
                  id="phone"
                  name="phone"
                  placeholder="Your phone number"
                  style={inputStyle}
                  value={formState.phone}
                  onChange={handleChange}
                  onFocus={(e) => e.target.style.borderColor = 'var(--primary)'}
                  onBlur={(e) => e.target.style.borderColor = 'rgba(255, 255, 255, 0.1)'}
                />
              </div>
              
              <div style={formGroupStyle}>
                <label htmlFor="message" style={labelStyle}>How can we help?</label>
                <textarea
                  id="message"
                  name="message"
                  placeholder="Tell us about your business needs"
                  required
                  style={textareaStyle}
                  value={formState.message}
                  onChange={handleChange}
                  onFocus={(e) => e.target.style.borderColor = 'var(--primary)'}
                  onBlur={(e) => e.target.style.borderColor = 'rgba(255, 255, 255, 0.1)'}
                />
              </div>
              
              <motion.button
                type="submit"
                style={buttonStyle}
                whileHover={{ 
                  backgroundColor: 'rgba(255, 255, 255, 0.05)',
                  transform: 'translateY(-3px)'
                }}
                whileTap={{ 
                  transform: 'translateY(0)'
                }}
              >
                Let's Talk
              </motion.button>
            </form>
          </div>
        </motion.div>
      </div>
    </section>
  );
} 