"use client";

import { motion } from 'framer-motion';
import { statsData } from '@/data/statsData';
import { useEffect, useRef, useState } from 'react';
import { 
  CheckCircle, 
  TrendingUp, 
  Zap
} from 'lucide-react';

export default function Stats() {
  const [isInView, setIsInView] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsInView(true);
          observer.disconnect();
        }
      },
      { threshold: 0.2 }
    );

    if (ref.current) {
      observer.observe(ref.current);
    }

    return () => {
      if (ref.current) {
        observer.unobserve(ref.current);
      }
    };
  }, []);

  // Counter animation for the stats
  const Counter = ({ value, prefix = "", suffix = "" }: { value: number; prefix?: string; suffix?: string }) => {
    const [count, setCount] = useState(0);
    
    useEffect(() => {
      if (!isInView) return;
      
      let start = 0;
      const end = value;
      const duration = 2500; // 2.5 seconds for slower counting
      const increment = end / (duration / 16); // Update roughly every 16ms (60fps)
      
      const timer = setInterval(() => {
        start += increment;
        if (start >= end) {
          setCount(end);
          clearInterval(timer);
        } else {
          setCount(Math.floor(start));
        }
      }, 16);
      
      return () => clearInterval(timer);
    }, [isInView, value]);
    
    return (
      <span>
        {prefix}{count}{suffix}
      </span>
    );
  };

  const features = [
    {
      id: 1,
      icon: <CheckCircle className="w-8 h-8 text-primary" />,
      title: "Identify Pain Points",
      description: "Uncover and fix inefficiencies that are holding you back, streamlining your path to growth."
    },
    {
      id: 2,
      icon: <Zap className="w-8 h-8 text-primary" />,
      title: "Eliminate Bottlenecks",
      description: "Streamline your workflows for smooth, efficient, and hassle-free operations."
    },
    {
      id: 3,
      icon: <TrendingUp className="w-8 h-8 text-primary" />,
      title: "High ROI & Scalability",
      description: "Grow confidently with flexible solutions that expand alongside your business."
    }
  ];

  return (
    <section className="section bg-background" id="why-us">
      <div className="container">
        <motion.div 
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.3 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          className="stylish-box text-center mb-16"
          style={{ 
            position: 'relative',
            zIndex: 1 
          }}
        >
          <h2 className="text-3xl md:text-4xl font-bold mb-4">WHY CLIENTS PREFER US</h2>
          <p className="text-2xl font-bold mb-4">
            FNX Labs is Your Trusted Partner for AI-Powered Automation
          </p>
          <p className="text-muted-100 dark:text-muted-200 max-w-xl mx-auto">
            Want to spend less time on administrative tasks and more time Building your business? Here's how we deliver on that promise
          </p>
        </motion.div>

        <motion.div 
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          viewport={{ once: true, amount: 0.1 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-16"
        >
          {features.map((feature, index) => (
            <motion.div
              key={feature.id}
              initial={{ opacity: 0, y: 50 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.8, ease: "easeOut", delay: index * 0.1 }}
              className="feature-box text-center"
            >
              <div className="feature-icon mx-auto">
                {feature.icon}
              </div>
              <h3 className="feature-title">{feature.title}</h3>
              <p className="feature-description">
                {feature.description}
              </p>
            </motion.div>
          ))}
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          className="text-center max-w-2xl mx-auto"
        >
          <p className="text-xl font-medium">
            We craft a tailored action plan that fits your budget and needsâ€”no guesswork, just solutions.
          </p>
        </motion.div>
      </div>
    </section>
  );
} 