"use client";

import Link from 'next/link';
import { motion } from 'framer-motion';
import { CSSProperties } from 'react';
import { useState, useEffect } from 'react';
import Image from 'next/image';

export default function Hero() {
  // Initialize responsive state
  const [isLargeScreen, setIsLargeScreen] = useState(false);
  
  // Move window checks to useEffect
  useEffect(() => {
    const handleResize = () => {
      setIsLargeScreen(window.innerWidth >= 1024);
    };
    
    // Set initial value
    handleResize();
    
    // Add event listener
    window.addEventListener('resize', handleResize);
    
    // Clean up
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Animated text variants for the heading
  const headingVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.08,
        delayChildren: 0.3
      }
    }
  };

  const letterVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { 
        type: "spring", 
        damping: 12,
        stiffness: 100
      }
    }
  };

  // Split text for animated rendering - keep "Stronger" on one line
  const heroTitle = "Smarter Systems,";
  const heroSecondLine = "Stronger Businesses";
  const heroTitleArray = heroTitle.split("");
  const heroSecondLineArray = heroSecondLine.split("");

  const primaryButtonStyle: CSSProperties = {
    padding: '0.75rem 2rem',
    backgroundColor: 'transparent',
    color: 'var(--primary)',
    border: '1px solid var(--primary)',
    fontWeight: '300',
    display: 'inline-block',
    textAlign: 'center',
    transition: 'all 0.3s',
    textDecoration: 'none',
    fontSize: '0.85rem',
    letterSpacing: '0.15em',
    textTransform: 'uppercase',
    position: 'relative',
    overflow: 'hidden'
  };

  const outlineButtonStyle: CSSProperties = {
    padding: '0.75rem 2rem',
    backgroundColor: 'transparent',
    color: 'var(--foreground)',
    border: '1px solid rgba(255, 255, 255, 0.2)',
    fontWeight: '300',
    display: 'inline-block',
    textAlign: 'center',
    transition: 'all 0.3s',
    textDecoration: 'none',
    fontSize: '0.85rem',
    letterSpacing: '0.15em',
    textTransform: 'uppercase'
  };

  // Grid settings based on responsive state
  const gridStyle: CSSProperties = {
    display: 'grid',
    gridTemplateColumns: isLargeScreen ? 'repeat(2, 1fr)' : '1fr',
    gap: '4rem',
    alignItems: 'center'
  };

  // Gradient text effect style
  const gradientTextStyle: CSSProperties = {
    fontFamily: 'var(--font-display)',
    fontWeight: '700',
    position: 'relative',
    display: 'inline-block',
    color: 'var(--primary)',
    backgroundImage: 'linear-gradient(45deg, var(--primary), #ffffff)',
    backgroundSize: '100%',
    backgroundRepeat: 'repeat',
    WebkitBackgroundClip: 'text',
    WebkitTextFillColor: 'transparent',
    textShadow: '0 0 20px rgba(var(--primary-rgb), 0.3)',
    paddingBottom: '0.25rem',
    borderBottom: '1px solid var(--primary)',
    textTransform: 'uppercase',
    letterSpacing: '0.1em'
  };

  // Shimmer animation for text
  const shimmerStyle: CSSProperties = {
    position: 'relative',
    overflow: 'hidden',
    display: 'inline-block'
  };

  return (
    <section id="hero" style={{ 
      backgroundColor: 'var(--background)', 
      paddingTop: '7rem', 
      paddingBottom: '6rem', 
      marginTop: 'var(--nav-height)',
      position: 'relative',
      zIndex: 10,
      borderBottom: '1px solid rgba(255, 255, 255, 0.05)'
    }}>
      <div style={{ 
        maxWidth: '80rem', 
        marginLeft: 'auto', 
        marginRight: 'auto', 
        paddingLeft: '1.5rem', 
        paddingRight: '1.5rem', 
        paddingTop: '2rem' 
      }}>
        <div style={gridStyle}>
          <motion.div
            initial={{ opacity: 0, y: 50 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, ease: "easeOut" }}
            viewport={{ once: true, amount: 0.3 }}
            style={{ 
              position: 'relative',
              zIndex: 1,
              textAlign: isLargeScreen ? 'left' : 'center'
            }}
          >
            {/* Animated letter-by-letter text */}
            <motion.h1 
              variants={headingVariants}
              initial="hidden"
              animate="visible"
              style={{ 
                fontSize: '3rem', 
                fontWeight: '400', 
                marginBottom: '1.5rem', 
                lineHeight: '1.2',
                letterSpacing: '0.05em',
                overflow: 'hidden'
              }}
            >
              {heroTitleArray.map((letter, index) => (
                <motion.span
                  key={index}
                  variants={letterVariants}
                  style={{ display: 'inline-block', whiteSpace: 'pre' }}
                >
                  {letter}
                </motion.span>
              ))}
              <br />
              {heroSecondLineArray.map((letter, index) => (
                <motion.span
                  key={`second-${index}`}
                  variants={letterVariants}
                  style={{ display: 'inline-block', whiteSpace: 'pre' }}
                >
                  {letter}
                </motion.span>
              ))}
              <br />
              <motion.span 
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 1.3, duration: 0.8 }}
                style={shimmerStyle}
              >
                <span style={gradientTextStyle}>
                  with Cutting-Edge AI Solutions
                </span>
                <motion.span
                  style={{
                    position: 'absolute',
                    top: 0,
                    right: 0,
                    width: '100%',
                    height: '100%',
                    background: 'linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent)',
                    zIndex: 10
                  }}
                  animate={{
                    x: ['100%', '-100%']
                  }}
                  transition={{
                    repeat: Infinity,
                    repeatType: 'loop',
                    duration: 3,
                    ease: "easeInOut",
                    delay: 2
                  }}
                />
              </motion.span>
            </motion.h1>
            <motion.p 
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1.5, duration: 1 }}
              style={{ 
                fontSize: '1.1rem', 
                marginBottom: '3rem', 
                color: 'var(--muted-100)', 
                maxWidth: '42rem', 
                marginLeft: isLargeScreen ? '0' : 'auto', 
                marginRight: isLargeScreen ? '0' : 'auto',
                lineHeight: '1.8',
                fontWeight: '300',
                letterSpacing: '0.025em',
                textShadow: '0 0 30px rgba(0, 0, 0, 0.8)'
              }}
            >
              We make AI Agents easy and efficient, so you can grow your business hassle free - while focusing on what truly matters.
            </motion.p>
            <motion.div 
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1.7, duration: 1 }}
              style={{ 
                display: 'flex', 
                flexDirection: isLargeScreen ? 'row' : 'column', 
                gap: '1.25rem', 
                justifyContent: isLargeScreen ? 'flex-start' : 'center',
                maxWidth: isLargeScreen ? 'none' : '24rem',
                margin: isLargeScreen ? '0' : '0 auto'
              }}
            >
              <Link 
                href="/#contact" 
                style={primaryButtonStyle}
                onMouseEnter={(e) => {
                  e.currentTarget.style.backgroundColor = 'rgba(255, 255, 255, 0.05)';
                  e.currentTarget.style.transform = 'translateY(-3px)';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.backgroundColor = 'transparent';
                  e.currentTarget.style.transform = 'translateY(0)';
                }}
              >
                Work With Us Today
              </Link>
              <Link 
                href="/#features" 
                style={outlineButtonStyle}
                onMouseEnter={(e) => {
                  e.currentTarget.style.backgroundColor = 'rgba(255, 255, 255, 0.02)';
                  e.currentTarget.style.transform = 'translateY(-3px)';
                  e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.3)';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.backgroundColor = 'transparent';
                  e.currentTarget.style.transform = 'translateY(0)';
                  e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.2)';
                }}
              >
                Explore Services
              </Link>
            </motion.div>
          </motion.div>
          
          {/* Only render the visual element for larger screens or if we've determined screen size */}
          {(isLargeScreen || typeof window !== 'undefined') && (
            <motion.div
              initial={{ opacity: 0, scale: 0.95 }}
              whileInView={{ opacity: 1, scale: 1 }}
              transition={{ duration: 1, ease: "easeOut", delay: 0.2 }}
              viewport={{ once: true, amount: 0.3 }}
              style={{ 
                position: 'relative',
                zIndex: 1,
                display: 'flex',
                justifyContent: 'center'
              }}
            >
              <div style={{ 
                position: 'relative', 
                width: '100%', 
                maxWidth: '36rem', 
                height: '400px', 
                background: 'linear-gradient(to bottom right, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.05))', 
                border: '1px solid rgba(255, 255, 255, 0.1)',
                overflow: 'hidden'
              }}>
                <div style={{ position: 'absolute', inset: 0, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                  <div style={{ textAlign: 'center' }}>
                    <motion.div 
                      style={{ 
                        width: '10rem', 
                        height: '10rem', 
                        margin: '0 auto',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        position: 'relative'
                      }}
                      animate={{
                        scale: [1, 1.05, 1],
                        opacity: [0.9, 1, 0.9]
                      }}
                      transition={{
                        duration: 3,
                        repeat: Infinity,
                        ease: "easeInOut"
                      }}
                    >
                      {/* FNX Logo - white on black background */}
                      <Image 
                        src="/fnx-logo.png" 
                        alt="FNX Labs Logo" 
                        width={200} 
                        height={200}
                        style={{
                          width: '100%',
                          height: 'auto'
                        }}
                      />
                    </motion.div>
                    <motion.h2 
                      style={{ 
                        fontSize: '1.5rem', 
                        fontWeight: '700', 
                        marginTop: '2rem', 
                        color: 'var(--primary)',
                        letterSpacing: '0.2em',
                        textTransform: 'uppercase',
                        fontFamily: 'var(--font-display)'
                      }}
                      animate={{
                        color: ['var(--primary)', '#ffffff', 'var(--primary)']
                      }}
                      transition={{
                        duration: 3,
                        repeat: Infinity,
                        ease: "easeInOut",
                        delay: 0.5
                      }}
                    >
                      FNXLabs
                    </motion.h2>
                    <motion.p 
                      style={{ 
                        marginTop: '0.75rem', 
                        color: 'var(--muted-100)',
                        fontWeight: '300',
                        letterSpacing: '0.1em',
                        fontSize: '0.9rem',
                        fontFamily: 'var(--font-alt)'
                      }}
                      animate={{
                        opacity: [0.7, 1, 0.7]
                      }}
                      transition={{
                        duration: 3,
                        repeat: Infinity,
                        ease: "easeInOut",
                        delay: 0.75
                      }}
                    >
                      AI-Powered Solutions
                    </motion.p>
                  </div>
                </div>
              </div>
            </motion.div>
          )}
        </div>
      </div>
    </section>
  );
} 