"use client";

import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Check, Shield, Clock, Zap, Users, Layers } from 'lucide-react';
import { CSSProperties } from 'react';

interface FeatureItemProps {
  icon: React.ReactNode;
  title: string;
  description: string;
}

const FeatureItem = ({ icon, title, description }: FeatureItemProps) => {
  const featureBoxStyle: CSSProperties = {
    padding: '2.5rem',
    backgroundColor: 'transparent',
    position: 'relative',
    height: '100%',
    borderLeft: '1px solid rgba(255, 255, 255, 0.1)',
    transition: 'all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275)'
  };

  const iconContainerStyle: CSSProperties = {
    width: '3rem',
    height: '3rem',
    backgroundColor: 'transparent',
    border: '1px solid rgba(255, 255, 255, 0.2)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: '1.5rem',
    transition: 'all 0.4s ease'
  };

  const titleStyle: CSSProperties = {
    fontSize: '1.125rem',
    fontWeight: '300',
    marginBottom: '1rem',
    color: 'var(--primary)',
    letterSpacing: '0.1em',
    textTransform: 'uppercase',
    fontFamily: 'var(--font-display)'
  };

  const descriptionStyle: CSSProperties = {
    fontSize: '0.95rem',
    color: 'var(--muted-100)',
    lineHeight: '1.7',
    fontWeight: '300',
    fontFamily: 'var(--font-alt)'
  };

  // Hover animation overlay
  const overlayStyle: CSSProperties = {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: 'linear-gradient(135deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0) 100%)',
    opacity: 0,
    transition: 'opacity 0.4s ease, transform 0.4s ease',
    transform: 'scale(0.95)',
    pointerEvents: 'none',
    zIndex: 0
  };

  return (
    <motion.div
      whileHover={{ 
        y: -10,
        borderLeft: '1px solid rgba(255, 255, 255, 0.3)',
        boxShadow: '0 20px 40px rgba(0, 0, 0, 0.3)',
        transform: 'perspective(1000px) rotateX(2deg) rotateY(2deg)'
      }}
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6 }}
      viewport={{ once: true, amount: 0.3 }}
      style={featureBoxStyle}
    >
      {/* Hover overlay */}
      <motion.div 
        style={overlayStyle} 
        whileHover={{ 
          opacity: 1,
          transform: 'scale(1)'
        }}
      />
      
      <div style={{ position: 'relative', zIndex: 1 }}>
        <motion.div 
          style={iconContainerStyle}
          whileHover={{
            borderColor: 'var(--primary)',
            transform: 'rotate(10deg)'
          }}
        >
          {icon}
        </motion.div>
        <h3 style={titleStyle}>{title}</h3>
        <p style={descriptionStyle}>{description}</p>
      </div>
    </motion.div>
  );
};

export default function Features() {
  // Initialize with mobile layout as default
  const [gridColumns, setGridColumns] = useState('repeat(1, 1fr)');
  
  // Use useEffect to handle client-side window detection
  useEffect(() => {
    // Update grid columns based on window width
    const handleResize = () => {
      if (window.innerWidth >= 1024) {
        setGridColumns('repeat(3, 1fr)');
      } else if (window.innerWidth >= 768) {
        setGridColumns('repeat(2, 1fr)');
      } else {
        setGridColumns('repeat(1, 1fr)');
      }
    };
    
    // Set initial value
    handleResize();
    
    // Add event listener for window resize
    window.addEventListener('resize', handleResize);
    
    // Clean up event listener
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const sectionStyle: CSSProperties = {
    backgroundColor: 'var(--background)',
    paddingTop: '8rem',
    paddingBottom: '8rem',
    position: 'relative',
    zIndex: 8,
    borderBottom: '1px solid rgba(255, 255, 255, 0.05)'
  };

  const containerStyle: CSSProperties = {
    maxWidth: '80rem',
    marginLeft: 'auto',
    marginRight: 'auto',
    paddingLeft: '1.5rem',
    paddingRight: '1.5rem'
  };

  const headingStyle: CSSProperties = {
    fontSize: '2.5rem',
    fontWeight: '200',
    marginBottom: '1rem',
    textAlign: 'center',
    letterSpacing: '0.1em'
  };

  const accentSpanStyle: CSSProperties = {
    position: 'relative',
    color: 'var(--primary)',
    fontWeight: '300',
    display: 'inline-block',
    borderBottom: '1px solid var(--primary)',
    paddingBottom: '0.25rem'
  };

  const subheadingStyle: CSSProperties = {
    fontSize: '1.1rem',
    color: 'var(--muted-100)',
    marginBottom: '5rem',
    maxWidth: '42rem',
    marginLeft: 'auto',
    marginRight: 'auto',
    textAlign: 'center',
    lineHeight: '1.8',
    fontWeight: '300',
    letterSpacing: '0.025em'
  };

  const gridStyle: CSSProperties = {
    display: 'grid',
    gridTemplateColumns: gridColumns,
    gap: '0',
    border: '1px solid rgba(255, 255, 255, 0.05)',
    backgroundColor: 'rgba(255, 255, 255, 0.01)'
  };

  const iconStyle: CSSProperties = {
    color: 'var(--primary)',
    width: '1.5rem',
    height: '1.5rem',
    strokeWidth: 1
  };

  return (
    <section id="features" style={sectionStyle}>
      <div style={containerStyle}>
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
        >
          <h2 style={headingStyle}>
            Our <span style={accentSpanStyle}>Features</span>
          </h2>
          <p style={subheadingStyle}>
            Discover how our AI solutions can transform your business operations and drive growth with elegance, precision, and intelligence.
          </p>
        </motion.div>

        <motion.div 
          style={gridStyle}
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.2 }}
          viewport={{ once: true }}
        >
          <FeatureItem
            icon={<Shield style={iconStyle} />}
            title="Enterprise Security"
            description="Your data is protected with state-of-the-art encryption and security protocols that meet industry standards."
          />
          <FeatureItem
            icon={<Clock style={iconStyle} />}
            title="24/7 Automation"
            description="Our AI systems work around the clock, ensuring continuous processing and monitoring without interruption."
          />
          <FeatureItem
            icon={<Zap style={iconStyle} />}
            title="Performance"
            description="Experience unprecedented speed with our optimized algorithms designed for maximum efficiency."
          />
          <FeatureItem
            icon={<Users style={iconStyle} />}
            title="Collaborative"
            description="Enhance team productivity with collaborative AI tools that integrate seamlessly with your workflow."
          />
          <FeatureItem
            icon={<Layers style={iconStyle} />}
            title="Scalable"
            description="Our solutions grow with your business, scaling effortlessly to meet increasing demands."
          />
          <FeatureItem
            icon={<Check style={iconStyle} />}
            title="Custom Integration"
            description="Tailor-made solutions that integrate perfectly with your existing systems and processes."
          />
        </motion.div>
      </div>
    </section>
  );
} 